import Vue from 'vue'
import Router from 'vue-router'

import Nav from './components/navbar.vue' 
import Intro from './components/intro.vue'
import About from './components/about.vue'
import Service from './components/service.vue'
import Counter from './components/counter.vue'
import Portfolio from './components/projects.vue'
import Praise from './components/testimonial.vue'
import Blog from './components/blogs.vue'
import Footer from './components/footer.vue'
Vue.use(Router)

export default new Router({
    mode: 'hash',
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Intro,      
            meta: {
              title: 'Home Page', 
            },
        },
        {
            path:'/about',
            name: 'About',
            component: About
        },

        {
            path:'/service',
            name: 'Service',
            component: Service
        },
        {
            path:'/projects',
            name: 'Portfolio',
            component: Portfolio
        },
        {
            path: '/counter',
            name: 'Counter',
            component: Counter
        },
        {
            path: '/testimonial',
            name: 'Testimonial',
            component: Praise
        },
        {
            path:'/blog',
            name: 'Blog',
            component: Blog
        },
        {
            path: '/footer',
            name: 'Footer',
            component: Footer
        }
        
    ]

})

